// классы отображения форм оформления заказа
import { Form } from './Form';
// шаг 1. форма выбора способа оплаты и адреса доставки
export class Order extends Form {
    constructor(container, events) {
        super(container, events);
        this._buttonOnline = this.container.elements.namedItem('online');
        this._buttonCash = this.container.elements.namedItem('cash on delivery');
        this._buttonOnline.addEventListener('click', () => {
            this._buttonOnline.classList.add('button_alt-active');
            this._buttonCash.classList.remove('button_alt-active');
            this.onFieldChange('payment', this._buttonOnline.name);
        });
        this._buttonCash.addEventListener('click', () => {
            this._buttonOnline.classList.remove('button_alt-active');
            this._buttonCash.classList.add('button_alt-active');
            this.onFieldChange('payment', this._buttonCash.name);
        });
    }
    set payment(value) {
        if (value) {
            this.toggleClass(this.container.elements.namedItem(value), 'button_alt-active', true);
        }
    }
    set address(value) {
        this.container.elements.namedItem('address').value =
            value;
    }
}
// шаг2. форма ввода email и номера телефона
export class Contacts extends Form {
    constructor(container, events) {
        super(container, events);
    }
    set phone(value) {
        this.container.elements.namedItem('phone').value =
            value;
    }
    set email(value) {
        this.container.elements.namedItem('email').value =
            value;
    }
}
